@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
::selection{background-color:#b629f2b5}body{margin:0;font-family:'Roboto', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#101320}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}



.Navbar{background-color:#161A26;padding:1.25rem;display:flex;justify-content:space-between}.logo{height:80px}.button{display:inline-block;color:#fff;font-size:1.5rem;padding:1.5rem;border-bottom:2px solid #161A26;transition:all 0.3s ease-in-out;cursor:pointer}.button:hover{border-bottom:2px solid #fff}

.Jumbotron{width:100%;height:500px;background-image:url(https://wallpaperaccess.com/full/3723585.jpg);background-position:center;background-size:cover;position:absolute}.dots{height:100%;width:100%;position:absolute;z-index:1;margin-top:-45px;z-index:1}.dots:after{content:'';display:block;position:absolute;left:0;right:0;top:0;bottom:0;background:rgba(0,0,0,0.137);background-image:radial-gradient(black 33%, transparent 33%);background-size:3px 3px;z-index:1}.search-bar{width:60%;height:50px;background-color:#fff;position:relative;bottom:-70%;margin-left:auto;margin-right:auto}.search-bar select{border:none;width:80%;height:100%;font-size:large}.search-bar option{color:red}.waves1{fill:#161A26 !important;width:100%;height:45px;z-index:9999;position:relative}.wavesBox{overflow:hidden;position:absolute;bottom:-10px;width:100%}.waves{fill:#161A26 !important;width:100%;height:45px;-webkit-transform:rotate(180deg);transform:rotate(180deg);z-index:9999;position:relative}.ogloszenia{color:white;font-weight:bold;font-size:8vw;width:100%;text-align:center;margin:0 auto;position:absolute;top:50%;z-index:99999}@media (min-width: 600px){.ogloszenia{font-size:6vw}}@media (min-width: 900px){.ogloszenia{font-size:4vw}}

.CarCards{margin-top:550px;width:100%}.vip{border:3px solid gold}.card{background-color:#161A26;color:white;position:relative;height:300px;box-sizing:border-box;overflow:hidden;transition:all 0.2s ease-out}.card .moreInfoIcon{position:absolute;top:0;right:0;padding:0.75rem;font-size:1.25rem;z-index:4;cursor:pointer}.card .countryOfOrigin{position:absolute;top:0;right:0;padding:0.75rem;font-size:1.25rem;z-index:4;right:2.25rem}.card .more-info{width:100%;height:100%;position:absolute;z-index:3;display:block;right:0;transition:all 0.5s ease-in;background-color:#161A26}.card .more-info.disabled{right:-100%}.card .vipIcon{position:absolute;top:0;padding:0.75rem;font-size:1.25rem;color:gold}.card .imgBox{width:100%;height:100%;border-radius:5px 5px 0px 0px;background-position:center;background-size:cover}.card .cardTitle{font-size:1rem;font-weight:bold;padding:0.75rem;position:absolute;z-index:1;bottom:0;cursor:pointer}.card .cardTitle:first-letter{text-transform:uppercase}.card .details{font-weight:300}.card .verified{padding:0.75rem;position:absolute;bottom:0;right:0;font-size:1.25em;z-index:2}.cards{max-width:1200px;margin:0 auto;display:grid;grid-gap:1rem}@media (min-width: 100px){.cards{grid-template-columns:repeat(1, 1fr);max-width:450px}}@media (min-width: 600px){.cards{grid-template-columns:repeat(1, 1fr);max-width:500px}}@media (min-width: 900px){.cards{grid-template-columns:repeat(2, 1fr);max-width:800px}}@media (min-width: 1100px){.cards{grid-template-columns:repeat(2, 1fr);max-width:900px}}@media (min-width: 1200px){.cards{grid-template-columns:repeat(3, 1fr);max-width:1200px}}.moreDetails{width:100%;height:100%;padding:1rem;display:flex;flex-direction:column;justify-content:center;align-items:center;font-size:1.5rem}.moreDetails .item{display:flex;justify-content:center;align-items:center}

footer{text-align:center;color:#fff;width:100%;margin:1.25rem 0 1.25rem 0}

